import React, { Component } from 'react';
import Modal from 'react-modal';
import Close from '../images/close.svg'

class ImageModal extends Component {
	constructor(props) {
		super(props);
		this.state = {
			isOpen: false,
		};
		this.openModal = this.openModal.bind(this);
		this.closeModal = this.closeModal.bind(this);
	}

	openModal() {
		this.setState({modalIsOpen: true});
	}

	closeModal() {
		this.setState({modalIsOpen: false});
	}
	render() {
		const { src, alt, className } = this.props;
		return (
		<div>
			<img 
				src={src} 
				alt={alt} 
				onClick={this.openModal} 
				className={className} 
				style={{cursor: 'pointer'}}
			/>
			<Modal
				isOpen={this.state.modalIsOpen}
				onRequestClose={this.closeModal}
				contentLabel="Expanded image"
				style={{
	              overlay: {
	                background: 'rgba(0, 0, 0, 0.6)'
	              },
	              content: {
	                borderRadius: 0,
	                bottom: 'none',
	                boxShadow: '0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19)'
	              }
	            }}
			>
				<div>
					<div className="close-button">
						<button onClick={this.closeModal}><img src={Close} alt="close"/></button>
					</div>
					<img src={src} alt={alt} style={{width: 'auto', maxWidth: '90vw', maxHeight: '80vh', height: 'auto', marginTop: '15px', display: 'block', margin: '0 auto'}}/>
				</div>
			</Modal>
		</div>
		);
	}
}

export default ImageModal;